<template>

      <div class="login-form">
        <div class="g-form">
          <div class="g-form-line">
            <span class="g-form-label"></span>
            <div class="g-form-input">
              <input type="text"
                     placeholder=""
                     v-model="usernameModel"
              >
            </div>
            <span class="g-form-error">{{userErrors.errorText}}</span>
          </div>
          <div class="g-form-line">
            <span class="g-form-label"></span>
            <div class="g-form-input">
              <input type="password"
                     placeholder=""
                     v-model="passwordModel"
              >
            </div>
            <span class="g-form-error">{{ passwordErrors.errorText }}</span>
          </div>
          <div class="g-form-line">
            <div class="g-form-btn">
              <a class="button" @click="onLogin"></a>
            </div>
          </div>
          <p>{{ errorText }}</p>
        </div>
    </div>
</template>

<script>
    export default {
        data(){
            return{
              usernameModel:'',
              passwordModel:'',
              errorText: ''
            }
        },
      methods:{
        onLogin () {
          if (!this.userErrors.status || !this.passwordErrors.status) {
            this.errorText = ''
          }
          else {
            this.errorText = ''
            this.$http.get('api/login')
              .then((res) => {
                this.$emit('has-log', res.data)
              }, (error) => {
                console.log(error)
              })
          }
        }
      },

      computed:{
         userErrors(){
             /**/
            let errorText,status
            if(!/@/g.test(this.usernameModel)) {
              status=false
              errorText='@'
            }else{
              status = true
              errorText= ''
            }
            /**/
           if (!this.userFlag) {
             errorText = '',
             this.userFlag = true
           }
            return{
              status,
              errorText
            }
         },
        passwordErrors () {
            /* */
          let errorText, status
          if (!/^\w{1,6}$/g.test(this.passwordModel)) {
            status = false
            errorText = '1-6'
          }
          else {
            status = true
            errorText = ''
          }
          /*true,*/
          if (!this.passwordFlag) {
            errorText = '',
            this.passwordFlag = true
          }
          return {
            status,
            errorText
          }
        }
      }
    }
</script>

<style>

</style>



