<template>
    <div >
      <div class="index-wrap">
        <div class="index-left">
          <div class="index-left-block">
            <h2 ></h2>
          <!--template-->
          <template v-for="product in productList">
            <h3>{{product.title}}</h3>
            <ul>
              <li v-for="item in product.list">
                <a :href="item.url">{{item.name}}</a>
                <!--hot-->
                <span v-if="item.hot" class="hot-tag">HOT</span>
              </li>
            </ul>
            <div v-if="!product.last" class="hr"></div>
          </template>
          </div>
          <img src="http://img.mukewang.com/596440630001f1ad12000460.jpg" alt="" style="width: 270px;height: 120px;padding: 10px 15px">

          <div class="index-left-block lastest-news">
            <h2></h2>
            <ul>
                <li v-for="item in newsList">
                  <a :href="item.url" class="new-item">{{ item.title }}</a>
                </li>
            </ul>
          </div>
        </div>
        <div class="index-right">
          <!--:slides="slides"-->
          <slide-show :slides="slides" :inv="slideSpeed" @onChange="doSomeOnSlideChange"></slide-show>
          <div class="index-board-list">
            <!--classline-lastitem.idclass-->
            <div  class="index-board-item"
                  v-for="(item, index) in boardList"
                  :class="[{'line-last' : index % 2 !== 0}, 'index-board-' + item.id]"
            >
              <div class="index-board-item-inner"></div>
              <h2>{{item.title}}</h2>
              <p>{{item.description}}</p>
              <div class="index-board-button">
                <a :href="item.url" class="button"></a>
              </div>
            </div>
          </div>
         </div>
      </div>
    </div>
</template>

<script >
  import slideShow from '../components/slideShow'

  export default {

      components:{
        slideShow
      },
      created:function () {
        this.$http.get('api/getNewsList').then(
            function (suc) {
              this.newsList = suc.data
            },function (err) {
              console.log(err)
            }
        )
      },
      methods:{
        doSomeOnSlideChange(){}
      },
      data () {
          return {
            slideSpeed:2000,
            slides: [
                /*require,webpack*/
              {
                src: require('../assets/slideShow/pic1.jpg'),
                title: 'xxx1',
                href: 'detail/analysis',
                cid:1
              },
              {
                src: require('../assets/slideShow/pic2.jpg'),
                title: 'xxx2',
                href: 'detail/count',
                cid:2
              },
              {
                src: require('../assets/slideShow/pic3.jpg'),
                title: 'xxx3',
                href: 'detail/publish',
                cid:3
              },
              {
                src: require('../assets/slideShow/pic4.jpg'),
                title: 'xxx4',
                href: 'detail/forecast',
                cid:4
              }
            ],
            boardList: [
              {
                title: '',
                description: '',
                id: 'car',
                toKey: 'count',
                saleout: false,
                url :'/detail/count '
              },
              {
                title: '',
                description: ' ,',
                id: 'earth',
                toKey: 'forecast',
                saleout: false,
                url :'/detail/forecast'
              },
              {
                title: '',
                description: '',
                id: 'loud',
                toKey: 'analysis',
                saleout: true,
                url :'/detail/analysis'
              },
              {
                title: '',
                description: '',
                id: 'hill',
                toKey: 'publish',
                saleout: false,
                url :'/detail/publish'
              }
            ],
            newsList:[
            ],
           productList:{
             pc:{
               title:'pc',
               list:[
                 {
                   name: '',
                   url: 'http://starcraft.com'
                 },
                 {
                   name: '',
                   url: 'http://warcraft.com'
                 },
                 {
                   name: '',
                   url: 'http://overwatch.com',
                   hot: true
                 },
                 {
                   name: '',
                   url: 'http://hearstone.com'
                 }

               ]
             },
             app:{
               title:'',
               last:true,
               list:[
                 {
                   name: '91',
                   url: 'http://weixin.com'
                 },
                 {
                   name: '',
                   url: 'http://twitter.com',
                   hot: true
                 },
                 {
                   name: '',
                   url: 'http://maps.com'
                 },
                 {
                   name: '',
                   url: 'http://phone.com'
                 }
               ]
             }
           }
        }
      }
    }
</script>

<style scoped>
  .index-wrap {
    width: 1200px;
    margin: 0 auto;
    overflow: hidden;
  }
  .index-left {
    float: left;
    width: 300px;
    text-align: left;
  }
  .index-right {
    float: left;
    width: 900px;
  }
  .index-left-block {
    margin: 15px;
    background: #fff;
    box-shadow: 0 0 1px #ddd;
  }
  .index-left-block .hr {
    margin-bottom: 20px;
  }
  .index-left-block h2 {
    background: #4fc08d;
    color: #fff;
    padding: 10px 15px;
    margin-bottom: 20px;
  }
  .index-left-block h3 {
    padding: 0 15px 5px 15px;
    font-weight: bold;
    color: #222;
  }
  .index-left-block ul {
    padding: 10px 15px;
  }
  .index-left-block li {
    padding: 5px;
  }
  .index-board-list {
    overflow: hidden;
  }
  .index-board-item {
    float: left;
    width: 400px;
    background: #fff;
    box-shadow: 0 0 1px #ddd;
    padding: 20px;
    margin-right: 20px;
    margin-bottom: 20px;
  }
  .index-board-item-inner {
    margin-right: 20px;
    float: left;
    min-height: 125px;
    padding-left: 120px;
  }
  .index-board-car .index-board-item-inner{
    background: url(../assets/images/1.png) no-repeat;
  }
  .index-board-loud .index-board-item-inner{
    background: url(../assets/images/2.png) no-repeat;
  }
  .index-board-earth .index-board-item-inner{
    background: url(../assets/images/3.png) no-repeat;
  }
  .index-board-hill .index-board-item-inner{
    background: url(../assets/images/4.png) no-repeat;
  }
  .index-board-item h2 {
    font-size: 18px;
    font-weight: bold;
    color: #000;
    margin-bottom: 15px;
  }
  .line-last {
    margin-right: 0;
  }
  .index-board-button {
    margin-top: 20px;
  }
  .lastest-news {
    min-height: 512px;
  }
  .hot-tag {
    background: red;
    color: #fff;
  }
  .new-item {
    display: inline-block;
    width: 230px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
</style>



