<template>
    <div >
      <div class="app-head">
        <div class="app-head-inner">
          <router-link :to="{path: '/' }">
            <img src="../assets/logo.png">
          </router-link>

          <div class="head-nav">
            <ul class="nav-list">
              <li>{{username}}</li>
              <li class="nav-pile"  v-if="username!==''">|</li>
              <li v-if="username!== ''"></li>
              <li @click="logClick"  v-if="username===''"></li>
              <li class="nav-pile">|</li>
              <li @click="regClick"  v-if="username===''"></li>
              <li class="nav-pile" v-if="username=== ''">|</li>
              <li @click="aboutClick"></li>
            </ul>
          </div>
        </div>
      </div>
      <div class="container">
        <keep-alive>
          <router-view></router-view>
        </keep-alive>
      </div>
      <div class="app-foot">
       <section class="footer2 ">
          <div class="footer2-box maginauto">
            <div class="footer2-top">
              <p>        </p>
              <p> | |BOOS | | | |1688 | | | |YunOS | | | | | | | | | |</p>
            </div>
            <div class="footer2-bottom">
              <div class="line1 clearfix">
                <p>B2-20110446</p>
                <p>[2015]0295-065</p>
                <p>  20146</p>
              </div>
              <p><>2012-0005</p>
              <p>&copy;2006-2017 TMALL.COM </p>
            </div>
          </div>
        </section>
      </div>
      <!--  @on-close  -->
      <my-dialog :is-show="isShowAboutDialog" @on-close="closeDialog('isShowAboutDialog')">
        <p> </p>
      </my-dialog>
      <my-dialog :is-show="isShowLogDialog" @on-close="closeDialog('isShowLogDialog')">
        <log-form @has-log="onSuccessLog">LogDialog</log-form>
      </my-dialog>
      <my-dialog :is-show="isShowRegDialog" @on-close="closeDialog('isShowRegDialog')">
        <reg-form>RegDialog</reg-form>
      </my-dialog>
    </div>
</template>

<script>
  import Dialog from './base/dialog'
  import LogForm from './logForm'
  import RegForm from './regForm'
    export default {
        components:{
            myDialog:Dialog,
            LogForm,
            RegForm


        },
        data(){
            return{
              isShowAboutDialog: false,
              isShowLogDialog: false,
              isShowRegDialog: false,
              username:''
            }

        },
      methods:{
            aboutClick(){
                this. isShowAboutDialog = true
            },
            closeDialog(attr){
              this[attr] = false
            },
            logClick(){
              this. isShowLogDialog = true
            },
            regClick(){
              this. isShowRegDialog = true
            },
        onSuccessLog(data){
             this.username = data.username
            this.closeDialog ('isShowLogDialog')
        }

      }
    }
</script>

<style>
  html, body, div, span, applet, object, iframe,
  h1, h2, h3, h4, h5, h6, p, blockquote, pre,
  a, abbr, acronym, address, big, cite, code,
  del, dfn, em, img, ins, kbd, q, s, samp,
  small, strike, strong, sub, sup, tt, var,
  b, u, i, center,
  dl, dt, dd, ol, ul, li,
  fieldset, form, label, legend,
  table, caption, tbody, tfoot, thead, tr, th, td,
  article, aside, canvas, details, embed,
  figure, figcaption, footer, header, hgroup,
  menu, nav, output, ruby, section, summary,
  time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
  }
  /* HTML5 display-role reset for older browsers */
  article, aside, details, figcaption, figure,
  footer, header, hgroup, menu, nav, section {
    display: block;
  }
  body {
    line-height: 1;
  }
  ol, ul {
    list-style: none;
  }
  blockquote, q {
    quotes: none;
  }
  blockquote:before, blockquote:after,
  q:before, q:after {
    content: '';
    content: none;
  }
  table {
    border-collapse: collapse;
    border-spacing: 0;
  }
  a {
    color: inherit;
    text-decoration: none;
  }
  body {
    background: #f0f2f5;
    font-family: "Helvetica Neue",Helvetica,Arial,"Hiragino Sans GB","Hiragino Sans GB W3","Microsoft YaHei UI","Microsoft YaHei","WenQuanYi Micro Hei",sans-serif;
    font-size: 14px;
    color: #444;
  }
  .app-head {
    background: #363636;
    color: #b2b2b2;
    height: 90px;
    line-height: 90px;
    width: 100%;
  }
  .app-head-inner {
    width: 1200px;
    margin: 0 auto;
  }
  .head-logo {
    float: left;
  }
  .app-head-inner img {
    width: 50px;
    margin-top: 20px;
  }
  .head-nav {
    float: right;
  }
  .head-nav ul {
    overflow: hidden;
  }
  .head-nav li {
    cursor: pointer;
    float: left;
  }
  .nav-pile {
    padding: 0 10px;
  }
  .app-foot {
    text-align: center;
    height: 80px;
    width: 100%;
    line-height: 80px;
    background: #e3e4e8;
    clear: both;
    margin-top: 30px;
  }
  .container {
    width: 1200px;
    margin: 0 auto;
  }
  .hr {
    height: 1px;
    width: 100%;
    background: #ddd;
  }
  .button {
    background: #4fc08d;
    color: #fff;
    display: inline-block;
    padding: 10px 20px;
    cursor: pointer;
  }
  .button:hover {
    background: #4fc08d;
  }
  .g-form {

  }
  .g-form-line {
    padding: 15px 0;
  }
  .g-form-label {
    width: 100px;
    font-size: 16px;
    display: inline-block;
  }
  .g-form-input {
    display: inline-block;
  }
  .g-form-input input {
    height: 30px;
    width: 200px;
    line-height: 30px;
    vertical-align: middle;
    padding: 0 10px;
    border: 1px solid #ccc;
  }
  .g-form-btn {
    padding-left: 100px;
  }
  .g-form-error {
    color: red;
    padding-left: 15px;
  }
  .footer2{
    //height: 230px;
    background: #000000;
    padding-top: 12px;
    padding-left:10px ;

  }
  .footer2-top{
    margin-bottom: 9px;
  }
  .footer2-top p{
    font: 12px/25px "";
    color: #FFFFFF;
  }
  .footer2-bottom{
    color: #a4a4a4;
    font: 12px/20px "";
  }
  .line1 p{
    color: #a4a4a4;
    font: 12px/20px "";
    margin-right:40px ;
    float: left;
  }
  .footer2-bottom span{
    width: 73px;
    height: 30px;
    float: left;
    background: #000;
    margin-top:10px ;
    margin-right:14px;
  }
</style>



